@import "../global/variables.scss";

.main-nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  font-family: $main-font;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.22;
  color: $main-color;

  &__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 58px;

    @media (max-width: 1700px) {
      column-gap: 30px;
    }
  }

  &__item {
    list-style-type: none;

    &--current {
      color: $contrast-color;
    }
  }

  &__link {
    padding-top: 24px;
    padding-bottom: 24px;
    text-decoration: none;
    color: inherit;

    &:hover {
      opacity: 0.6;
    }
  }
}